@use 'style.utils' as danoUtils;

// Material-UI overrides.
.dano-app {
  .MuiInputBase-root {
    &.MuiInputBase-colorPrimary {
      .MuiInputBase-input.MuiOutlinedInput-input {
        border-color: var(--dano-fg-primary-color);
      }

      &.Mui-error {
        .MuiInputBase-input.MuiOutlinedInput-input {
          border-color: var(--dano-error-color);
        }
      }
    }
  }
  .MuiButtonBase-root {
    text-transform: none; // I just hate all-caps.

    color: var(--dano-fg-secondary-color);

    &.MuiButton-root.MuiButton-contained.MuiButton-containedPrimary {
      color: var(--dano-fg-secondary-color);
      background-color: var(--dano-bg-secondary-color);

      &:hover {
        background-color: danoUtils.dano-darken(var(--dano-bg-secondary-color));
      }
    }

    &.MuiButton-outlined {
      color: var(--dano-bg-secondary-color);
      border-color: danoUtils.dano-lighten(var(--dano-bg-secondary-color));

      &:hover {
        border-color: var(--dano-bg-secondary-color);
      }
    }

    &.Mui-selected {
      color: var(--dano-bg-secondary-color);
    }
  }

  // .MuiPaper-root {
  //   background-color: var(--dano-bg-secondary-color);
  //   color: var(--dano-fg-secondary-color);
  // }

  .MuiOutlinedInput-root {
    &.MuiInputBase-colorPrimary {
      color: var(--dano-fg-primary-color);

      .MuiOutlinedInput-notchedOutline {
        border-color: danoUtils.dano-lighten(var(--dano-fg-primary-color));
      }

      &:hover {
        .MuiOutlinedInput-notchedOutline {
          border-color: var(--dano-fg-primary-color);
        }
      }

      .MuiSvgIcon-root {
        color: inherit;
      }
    }
  }

  .MuiFormControl-root {
    .MuiFormLabel-root {
      &.MuiInputLabel-formControl {

      }
    }

    .MuiInputLabel-root {
      &.MuiFormLabel-colorPrimary {
        color: var(--dano-fg-primary-color);
        background-color: var(--dano-bg-primary-color);


      }
      &.MuiInputLabel-formControl {

      }
    }

  }
  .MuiTextField-root {
    .MuiFormLabel-root,
    .MuiInputLabel-root {
      color: danoUtils.dano-lighten(var(--dano-fg-primary-color));
    }

    .MuiOutlinedInput-root {
      .MuiOutlinedInput-notchedOutline {
        border-color: danoUtils.dano-lighten(var(--dano-fg-primary-color));
      }

      &:hover {
        .MuiOutlinedInput-notchedOutline {
          border-color: var(--dano-fg-primary-color);
        }
      }
    }

    .MuiInputBase-root {
      &.MuiInputBase-colorPrimary {
        color: var(--dano-fg-primary-color);

        .MuiSvgIcon-root {
          color: inherit;
        }
      }
      &.MuiInputBase-formControl {
        .MuiInputBase-input {
          &.MuiSelect-select {
            &.MuiSelect-multiple {
              &:has(.MuiChip-root) {
                // background-color: red;
              }
            }
          }

          .MuiChip-root {
            &.MuiChip-sizeMedium {
              color: var(--dano-fg-secondary-color);
              background-color: var(--dano-bg-secondary-color);
              font-size: 0.5rem;
              line-height: 1.2rem;
            }

            .MuiChip-label {
              &.MuiChip-labelMedium {
                padding: 0px 8px;
              }
            }
          }
        }
      }
    }
  }

  .MuiTabs-root {
    .MuiButtonBase-root {
      color: danoUtils.dano-lighten(var(--dano-fg-primary-color));

      &.Mui-selected {
        color: var(--dano-bg-secondary-color);
      }
    }

    .MuiTabs-scroller {
      .MuiTabs-indicator {
        background-color: var(--dano-bg-secondary-color);
      }

      .MuiTab-root {
        &.Mui-selected {
          &.MuiButtonBase-root {
          }
        }
      }
    }
  }

  .MuiDataGrid-root {
    color: var(--dano-fg-primary-color);

    .MuiButtonBase-root {
      color: var(--dano-fg-primary-color);

      &.Mui-disabled {
        color: disable(var(--dano-fg-primary-color));
      }
    }

    &.MuiDataGrid-withBorderColor {
      border-color: var(--dano-bg-secondary-color);

      .MuiDataGrid-main {
        .MuiDataGrid-columnHeaders.MuiDataGrid-withBorderColor {
          border-color: var(--dano-bg-secondary-color);

          .MuiDataGrid-columnSeparator {
            color: var(--dano-bg-secondary-color);
          }
        }
      }

      .MuiDataGrid-footerContainer {
        &.MuiDataGrid-withBorderColor {
          border-color: var(--dano-bg-secondary-color);
        }

        .MuiTablePagination-root {
          color: var(--dano-fg-primary-color);

          .MuiTablePagination-toolbar {
            .MuiTablePagination-spacer {
              color: var(--dano-fg-primary-color);
            }

            .MuiTablePagination-input {
              .MuiTablePagination-selectIcon {
                color: danoUtils.dano-lighten(var(--dano-fg-primary-color));
              }
            }

            .MuiTablePagination-actions {
            }
          }
        }
      }
    }

    .MuiDataGrid-row {
      .MuiDataGrid-withBorderColor {
        border-color: var(--dano-bg-secondary-color);
      }
    }
  }

  .MuiCircularProgress-root {
    &.MuiCircularProgress-colorPrimary {
      color: var(--dano-fg-primary-color);
    }
  }

  .MuiTableContainer-root {
    // background-color: var(--dano-bg-primary-color);
    // color: var(--dano-fg-primary-color);

    .MuiTable-root {
      .MuiTableBody-root {
        .MuiTableRow-root {
          .MuiTableCell-root {
            color: inherit;
            border-color: var(--dano-bg-secondary-color);
          }
        }
      }
    }
  }
}
